import React, { Component } from 'react';
import ReactTable from 'react-table';
import { withRouter } from 'react-router-dom';

import 'react-table/react-table.css';
import './Table.css';
import ExportExcel from '../../Home/Export/ExportExcel';
import ExportPdf from '../../Home/Export/ExportPdf';
import SVG from './FilterIcon';
import ClearIcon from './ClearIcon';
import CreateIcon from './CreateIcon';
import ExportSvg from './Export';
import Button from '../Button/Button';
import styles from './Table.module.css';
import PermissionRuler from '../../../hoc/PermissionRuler';
import Filter from '../../../containers/FilterFormView';
import RegistrationFormView from '../../../containers/RegistrationFormView';

class Table extends Component {
  state = {
    reorder: [],
    trigger: 0,
    dragged: null,
    show: false
  };

  handleShowHide = e => {
    e.preventDefault();
    this.setState(currentState => ({
      show: !currentState.show
    }));
  };
  handleDragging() {
    const headers = Array.prototype.slice.call(document.querySelectorAll('.table-header'));
    headers.forEach((header, i) => {
      header.setAttribute('draggable', true);
      header.ondragstart = e => {
        e.stopPropagation();
        this.setState({ dragged: i });
      };
      header.ondrag = e => e.stopPropagation;
      header.ondragend = e => {
        e.stopPropagation();
        setTimeout(() => {
          this.setState({ dragged: null });
        }, 1000);
      };

      header.ondragover = e => e.preventDefault();

      header.ondrop = e => {
        e.preventDefault();
        this.setState({ reorder: [...this.state.reorder, { a: i, b: this.state.dragged }] });
        this.setState({ trigger: Math.random() });
        localStorage.setItem('order', JSON.stringify(this.state.reorder));
      };
    });
  }

  componentDidMount() {
    this.handleDragging();
    if (!localStorage.order) return;
    const order = JSON.parse(localStorage.order);
    this.setState({ reorder: order });
  }
  componentDidUpdate() {
    this.handleDragging();
  }

  getTrProps = (state, rowInfo, instance) => {
    if (rowInfo) {
      return {
        onClick: () => {
          let id = rowInfo.original.id;
          if (id) {
            return this.props.history.push(`/home/${id}`);
          }
        },
        style: {
          background:
            rowInfo.row.status === 'black_list'
              ? '#ffb3b1'
              : 'white' && (rowInfo.row.status !== 'black_list' && rowInfo.row.status === 'shortlisted')
              ? '#9ac48d'
              : 'white'
        }
      };
    }
    return {};
  };

  showFilter = () => {
    this.props.toggleFilter();
    this.props.toggleRegister(false);
  };
  showRegister = () => {
    this.props.toggleFilter(false);
    this.props.toggleRegister();
  };

  GetAllProfiles = () => {
    this.props.GetAllProfiles();
  };

  columns = [
    {
      Header: 'Projects',
      accessor: 'projects',
      width: 250
    },
    {
      Header: 'Profile',
      accessor: 'name',
      width: 150
    },
    {
      Header: 'Position',
      accessor: 'position',
      width: 200
    },
    {
      Header: 'Portfolio',
      accessor: 'portfolio',
      width: 200
    },
    {
      Header: 'Phone',
      accessor: 'phone',
      width: 130
    },
    {
      Header: 'Profile',
      accessor: 'profile',
      width: 150
    },

    {
      Header: 'Comment',
      accessor: 'comment',
      width: 350
    },
    {
      Header: 'Technologies',
      accessor: 'technologies',
      width: 250
    },
    {
      Header: 'English',
      accessor: 'english'
    },

    {
      Header: 'Source',
      accessor: 'source'
    },
    {
      Header: 'Status',
      accessor: 'status'
    },
    {
      Header: 'Expected Salary',
      accessor: 'salary',
      width: 50
    },
    {
      Header: 'Created',
      accessor: 'created_at'
    },
    {
      Header: 'Updated',
      accessor: 'updated_at'
    }
  ];

  render() {
    const { data } = this.props;
    const cols = this.columns.map(col => ({
      ...col,
      Header: (
        <div key={col.Header} className="table-header">
          {col.Header}
        </div>
      )
    }));

    if (data) {
      var dataToApply = data.map(item => {
        let str1 = '';
        let str2 = '';
        if (item.technologies) {
          item.technologies.forEach(i => {
            str1 += `#${i.title} `;
          });
        }
        if (item.projects) {
          item.projects.forEach(i => {
            str2 += `#${i.title} `;
          });
        }
        return {
          ...item,
          technologies: str1,
          projects: str2
        };
      });
    }

    this.state.reorder.forEach(o => cols.splice(o.a, 0, cols.splice(o.b, 1)[0]));

    return (
      <div>
        {this.props.showFilter === true ? <Filter form={'filter'} /> : null}
        {this.props.showRegister === true ? <RegistrationFormView form={'register'} /> : null}
        <ReactTable
          {...this.props}
          data={dataToApply}
          columns={cols}
          defaultPageSize={100}
          sortable={true}
          ref={r => (this.currentPage = r)}
          getTrProps={this.getTrProps}
        >
          {(state, filtredData, instance) => {
            const dataToExport = state.pageRows.map(profile => {
              return profile._original;
            });
            return (
              <>
                {!this.props.onProjectDetail && (
                  <div className={styles.flexBtns}>
                    <PermissionRuler roles={['admin', 'manager']}>
                      <Button className="button" onClick={() => this.showRegister()}>
                        Create Profile &nbsp;&nbsp; <CreateIcon />
                      </Button>
                    </PermissionRuler>
                    <Button className="button" onClick={() => this.showFilter()}>
                      Filter &nbsp;&nbsp; <SVG />
                    </Button>

                    <Button className="button" onClick={() => this.GetAllProfiles()}>
                      Clear Filter &nbsp;&nbsp;
                      <ClearIcon />
                    </Button>

                    <div className="exportBox">
                      <Button onClick={this.handleShowHide} className="button">
                        Export &nbsp;&nbsp;
                        <ExportSvg />
                      </Button>

                      <div
                        className="exportBox__buttons"
                        style={{
                          transform: this.state.show ? 'translateY(0vh)' : 'translateY(-20vh)',
                          opacity: this.state.show ? '1' : '0'
                        }}
                      >
                        <ExportExcel profiles={dataToExport} />
                        <ExportPdf profiles={dataToExport} />
                      </div>
                    </div>
                  </div>
                )}
                {filtredData()}
              </>
            );
          }}
        </ReactTable>
      </div>
    );
  }
}
export default withRouter(Table);
